'use client';
import Link from 'next/link';
import Image from 'next/image';
import { motion } from 'framer-motion';
import notFound from '../../public/assets/not-found.png';

export default function NotFound() {
  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.3
      }
    }
  };

  const itemVariants = {
    hidden: { y: 20, opacity: 0 },
    visible: {
      y: 0,
      opacity: 1,
      transition: {
        duration: 0.5
      }
    }
  };

  return (
    <div className="min-h-screen bg-white">
      <motion.div 
        className="lg:px-24 lg:py-24 md:py-20 md:px-44 px-4 py-24 items-center flex justify-center flex-col-reverse lg:flex-row md:gap-28 gap-16"
        variants={containerVariants}
        initial="hidden"
        animate="visible"
      >
        <div className="xl:pt-24 w-full xl:w-1/2 relative pb-12 lg:pb-0">
          <div className="relative">
            <motion.div 
              className="relative z-10"
              variants={itemVariants}
            >
              <motion.h1 
                className="my-2 text-gray-800 font-bold text-2xl"
                variants={itemVariants}
              >
                Looks like you've found the
                doorway to the great nothing
              </motion.h1>
              <motion.p 
                className="my-2 text-gray-800"
                variants={itemVariants}
              >
                Sorry about that! Please visit our homepage to get where you need to go.
              </motion.p>
              <motion.div variants={itemVariants}>
                <Link 
                  href="/"
                  className="inline-block sm:w-full lg:w-auto my-2 border rounded-md py-4 px-8 text-center bg-indigo-600 text-white hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-700 focus:ring-opacity-50 transition duration-300"
                >
                  Take me there!
                </Link>
              </motion.div>
            </motion.div>
            <motion.div 
              className="relative"
              variants={itemVariants}
            >
              <Image
                src={notFound}
                alt="404 illustration"
                width={500}
                height={300}
                className="relative z-0"
              />
            </motion.div>
          </div>
        </div>
        <motion.div 
          variants={itemVariants}
          className="relative"
        >
          <Image
            src="/group.png"
            alt="404 group illustration"
            width={500}
            height={500}
          />
        </motion.div>
      </motion.div>
    </div>
  );
}